@charset "utf-8";
*{padding: 0px;margin: 0px;list-style: none;}
body{font-family: "맑은 고딕",arial,sans-serif;font-size: 12px;color: #333;border: 1px solid black; width: 700px;margin: 0px auto;margin-bottom: 20px;}


.product_list{
  /* border: 1px solid black; */
  height: 180px;
  width: 620px;
  margin: 0px auto;
}
.product_list .p_img{width: 200px;padding: 15px;}
.product_list input{
  float: right;
  color: #fff;
  background-color: rgb(254, 201, 104);
  padding: 5px;
  width: 100px;
  border: none;
  transform: translateX(-50px);
  cursor: pointer;
}
.product_list input:hover{
  background-color: rgb(255, 157, 0);
}
.product_list img{
  /* width: 480px;
  height: 360px; */
  width: 100%;
  float: left;
  border: 1px solid black;
}

.product_list p{line-height: 35px;transform: translateX(20px);}
.wrap{
  /* background-image: url({`${process.env.PUBLIC_URL}/images/phone.png`}); */
}
.wrap h2{text-align: center; padding: 20px 0px;display: block;}
.wrap h2::after{
  display: block;
  content: "";
  height: 2px;
  margin-top: 5px;
  background: #000;
  width: 200px;
  margin: 0px auto;
  transform: translateY(20px);
}

.style{   
  /* background-color: rgb(203, 238, 251); */
  width:600px;
  padding:10px;
  /* border:3px solid #000; */
  height: 300px;
  position: relative;
  text-align: center;
  margin: 20px auto;
}
.style input{
  /* border: none; */
  width: 200px;
  padding: 5px;
  font-size: 12px;
  margin-top: 10px;
}
.style .image_name{
}
.style .addBtn{
  padding-top: 20px;
}
.style .addBtn input{
  background-color: rgb(17, 114, 225);
  color: #fff;
  border: none;
  padding: 5px;
  width: 100px;
}
